<template>
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            Description : {{ description }}
                        </h5>
                        <button v-on:click="ClosePopup($event)" type="button" class="close" data-original-title="Close"><em
                                class="fa fa-times"></em></button>
                    </div>
                    <div class="modal-body">
                    <div  v-if="!isCommentAvailable"   class="col-sm-12 col-md-12 col-lg-12 col-xl-12 px-2">
                      
                        <div class="border p-3">
                            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                                v-on:OnSubmit="onSubmit"></dynamic-form>
                            <span v-if="isFormSubmitted" class="mandatory">{{$t('MandatoryString')}}</span>
                        </div>

                    </div>
                    <div v-if="isCommentAvailable" class="col-lg-12">
                        <div class="">
                            <div class="row">
                                <div class="col-md-12 text-center text-danger  no_records">
                                    {{$t('NoRecordfound')}}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div id="CommentsData" v-if="!isCommentAvailable"  class="listing" style="max-height: 465px;overflow-y: auto;">
                        <div class="row" >
                            <div class="col-lg-12">
                                <div id="accordion" class="w-100 activity-tab">
                            <div class="card">                               
                                <div class="card-body">
                                    <div class="conversation-main">
                                       <div v-for="(item,index) in viewTransistData" :key="index" class="d-flex w-100 me-3">
                                            <template v-if="item.comment!=''">
                                                    <div class="conversation-time">
                                                        <span>{{ $options.filters.formatTimeFromDateTime(item.standardtUsertm) }}</span>
                                                     </div>
                                                     <div class="conversation-box-con">
                                                    <div class="conversation-box">
                                                        <div class="name-circle">
                                                            <img src="/Content/images/DefaultImages/avatar-icon.jpg"
                                                                class="img-circle w-100 avatar" alt="Image">
                                                        </div>
                                                        <div class="conversation-header addednote-ticket">
                                                            <span class="sender-department" style="margin-left:10px">
                                                                <span class="sender-name"></span>
                                                                <p>
                                                                    <span v-if="item.comment.length > 1" id="more-49">
                                                                        {{ item.comment }}
                                                                        <br>
                                                                    </span>
                                                                    <span v-else-if="item.comment.length < 49" id="less-49"
                                                                        style="display: none">
                                                                        <br>
                                                                        <a class="font12 readLessLink comments editbtn pullleft"
                                                                            value="49" href="javascript:;" title="less">
                                                                            less
                                                                        </a>
                                                                    </span>
                                                                </p>
                                                            </span>
                                                        </div>
                                                        <div v-if="item.catalogueName != null" class="conversation-content">
                                                            <div class="col-12 p-0 notesdata">
                                                                <h6 class="text-muted time"><i><b> {{$t('PRODUCT_NAME')}}:</b> <span
                                                                            class="text-danger">{{ item.catalogueName }}</span>
                                                                    </i></h6>
                                                            </div>
                                                        </div>
                                                        <div v-if="item.disputed_category != null"
                                                            class="conversation-content">
                                                            <div class="col-12 p-0 notesdata">
                                                                <h6 class="text-muted time"><i><b> {{$t('category_name')}}:</b> <span
                                                                            class="text-danger">{{ item.disputed_category }}</span>
                                                                    </i></h6>
                                                            </div>
                                                        </div>
                                                        <div class="conversation-content">
                                                            <div class="col-12 p-0 notesdata">
                                                                <h6 class="text-muted time"><i><b> {{$t('Status')}}:</b> <span
                                                                            class="text-danger">{{ item.operation_code }}</span>
                                                                    </i></h6>
                                                            </div>
                                                        </div>

                                                        <div class="conversation-content">
                                                            <div class="col-12 p-0 notesdata">
                                                                <h6 class="text-muted time"><i><b>{{ item.created_by }}</b>,
                                                                        {{ $t('PostedOn') }}: <span class="text-danger"
                                                                            title="">{{ $options.filters.formatDate(item.created_at) }} {{ item.postedTime  }}</span>
                                                                    </i></h6>
                                                            </div>
                                                        </div>

                                                        <div v-if="item.disputed_category != null"
                                                            class="conversation-content">
                                                            <div class="col-12 p-0 notesdata">
                                                                <h6 class="text-muted time"><em><b>{{$t('DisputeDate')}}</b> <span
                                                                            class="text-danger">{{ $options.filters.formatDate(item.disputed_date) }}</span>
                                                                </em></h6>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </template>
                                        </div>
                                    </div>
                                </div>                              
                            </div>
                        </div>
                            </div>
                        </div>                        
                    </div>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../service/DataService';
export default {
    props: {
        assetTransferdetailId: Number,
        assetTransferBatchId: Number,
        itemName: String,
        description:String
       // relatedInfoId:Number
    },
    data() {

        return {
            viewTransistData: [],
            disputeFields:true,
            InputTypeStatus:false,
            relatedInfoId: 0,
            locationId: 0,
            isCommentAvailable:false,
            isFormSubmitted: true,
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Category'),
                            name: "Category",
                            value: "",
                            placeholder: "",
                            visibility:true,
                            validationRules:{"required":true},
                            config: {
                                options: [{ value: "RESOLVED", name: "Resolved" }, { value: "PENDING", name: "Pending" },{ value: "OTHER", name: "Other" }],
                                onChange:this.changeField
                            },
                            
                        }

                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            visibility: false,
                            validationRules:{"required":true},

                            placeholder: "",
                            config: {
                                options: [{ value: "Lost", name: "Lost/Stolen" }, { value: "Damaged", name: "Damaged" },{ value: "Receivedatdestination", name: "Received at destination" },{ value: "Returntosource", name: "Return to source"}],
                              
                            }
                        }

                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Comment'),
                            name: "Comment",
                            value: "",
                            placeholder: "",
                            validationRules:{"required":true},

                        }

                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    visibility:true
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Clear All",
                    onClick: this.onCancel,
                    visibility:true
                }
                ,
                {
                    type: "button",
                    class: "btn btn-primary",
                    visibility: false,
                    value: "1",
                    text: "<i class='fa fa-close pr-2'></i>ReOpen All",
                    onClick: this.ReopenButton
                }
            ],
            
        }


    },
    created() {
        this.GetTransistData();
       
    },
   
    methods: {
        changeField(evt)
        { 
            debugger;
            var vm = this;
            if(vm.FormSchema[0].Data[0].value=="RESOLVED"){
                vm.FormSchema[1].Data[0].visibility=true;
                
            }
            else{
                vm.FormSchema[1].Data[0].visibility=false;
                
            }
            

        },
        ReopenButtonClick:function(){
            debugger
                vm=this;
                vm.disputeFields=false;

        },
        ReopenButton:function(ev){
        
            debugger;
            var vm = this;
            if(vm.viewTransistData[0].operation_code == "RESOLVED"){
                  vm.buttons[2].visibility=true;
                   vm.FormSchema[0].Data[0].visibility=true
                   vm.FormSchema[2].Data[0].visibility=true
                   vm.buttons[1].visibility=true;
                   vm.buttons[0].visibility=true; 
                   vm.buttons[0].class="btn btn-success ";
                   vm.buttons[1].class="btn btn-danger";
                   vm.buttons[2].class="btn btn-primary d-none";
            }
            else{
                vm.buttons[2].class="btn btn-primary";
                vm.buttons[0].class="btn btn-success  d-none";
                   vm.buttons[1].class="btn btn-danger d-none";
            }
            

            },  
            
            HideShowButtons(){
                debugger
                var vm = this;
                if(vm.viewTransistData[0].operation_code == "RESOLVED"){
                    vm.FormSchema[0].Data[0].visibility=false
                   vm.FormSchema[2].Data[0].visibility=false
                //    vm.buttons[0].visibility=false;
                //    vm.buttons[1].visibility=false;
                   vm.buttons[0].class="btn btn-success d-none";
                   vm.buttons[1].class="btn btn-danger d-none";
                   vm.buttons[2].class="btn btn-primary";
                   vm.isFormSubmitted=false;
            }
            else {
                 // vm.buttons[2].visibility=false;
                   vm.FormSchema[0].Data[0].visibility=true
                   vm.FormSchema[2].Data[0].visibility=true
                //    vm.buttons[1].visibility=true;
                //    vm.buttons[0].visibility=true; 
                vm.buttons[2].class="btn btn-primary d-none";
                   vm.buttons[0].class="btn btn-success";
                   vm.buttons[1].class="btn btn-danger";
                   vm.isFormSubmitted=true;
                  
            }

            },



            GetTransistData() {
            debugger;
            var vm = this
            var url = `relatedInfoId=${vm.relatedInfoId}&locationId=${vm.locationId}&itemName=${vm.itemName}&assetTransferDetailId=${vm.assetTransferdetailId}&assetTransferBatchId=${vm.assetTransferBatchId}`;
            debugger;
            DataService.GetViewCommentData(url).then(response => {
                if (response.data) {
                    debugger
                    //console.log('res', response.data)
                    vm.viewTransistData = response.data;
                    vm.checkCommentEmpty();
                    //isCommentAvailable=
                    this.HideShowButtons();
                }
            })

        },
        checkCommentEmpty:function(){

           var vm=this;
            let i=0
                    let count=0;
                    vm.viewTransistData.forEach(obj => {
                        i=++i;
                    if(obj.comment==''){
                            count=++count
                    }
                    });

                    if(i==count){
                    vm.isCommentAvailable=true;
                    }
                    else{
                        vm.isCommentAvailable=false;
                    }
        },
        ClosePopup: function (item) {
            debugger
            this.$emit('ClosePopup', item)
        },
        onCancel: function () {
           debugger
            var vm = this;
            vm.FormSchema[0].Data[0].value=''
            vm.FormSchema[1].Data[0].value=''
            vm.FormSchema[2].Data[0].value=''
            vm.FormSchema[3].Data[0].value=''
        },

       
      

        onSubmit : function(formfields){
            debugger
            var vm=this;
            vm.isLoading=true;
                const obj={
                    RelatedInfoId:vm.relatedInfoId,
                    OperationCode:formfields.Category,
                    Comment:formfields.Comment,
                    Status:formfields.Status,                    
                    ASSET_TRANSFER_BATCH_ID:vm.assetTransferBatchId,
                    ASSET_TRANSFER_DETAILS_ID:vm.assetTransferdetailId
                }
                var JsonObj=JSON.stringify(obj);
                DataService.ResolveDisputeProduct(obj).then(response=>{
                    debugger
                    if(response.data==true){
                        vm.ShowAlert(vm.$t("Resolve Disputed Product successfully"), "success", vm.$t('Alert'));
                        vm.ClosePopup();
                        vm.isLoading=false;
                       // vm.$parent.FetchData();
                    }else{
                        vm.isLoading=false;
                        vm.ShowAlert(vm.$t("Error while returning item."), "warning", vm.$t('Alert'));
                    }
                });
            
            
        }

    }

}
</script>
