<template>
    <section>
    <tg-list :IsShowAction="false"  
        :showCheckBox="false"
        :listType="['List']"
        ModuleName="Asset"
        SubModuleCode="Asset"
        IdentityColumn="ASSET_CATALOGUE_ID"
         HeaderText="Transferred List"
         :PageSize="PageSize"
        :ListData="TransfredList"
        :HeaderData="Headers"
        :widgets="widgets"
        :callbackfunction="FetchData"
        :HeaderButtons="listheaderbuttons"
        :TotalRecords="TotalRecords"      
        :SearchFields="leftSearchFields" 
        :RenderRowActionMethod="renderActions"
        :ListDataCallBackFunction="FetchData"       
        :LegendArray="LegendArray"        
        :SortExp="SortExp"
        :SortBy="SortBy"
        v-on:LeftsearchButtonClick="leftCommonSearch" 
        v-on:PagerButtonClick="pagerMethod"
        v-on:HeaderButtonClick="commonHeaderButtonClick" 
        v-on:ActionButtonClick="actionButtonClick"
        v-on:SortdataButtonClick="sortdata"
        
    >    
        <template v-slot:slotdata="{ data }">
               <template v-if="data.column.COLUMN_NAME == 'transfeR_QUANTITY'">
                <span class="text-dark" :title="data.row.NUMBER_OF_ITEM">
                    <i class="fa fa-list" aria-hidden="true"></i>
                    <span class="noti-circle noti-blue">{{ data.row[data.column.COLUMN_NAME] }}</span>
                </span>
            </template> 
            <template v-if="data.column.COLUMN_NAME=='description'">
                <span class="text-dark" :title="data.row.NUMBER_OF_ITEM">
                    <a href="javascript:;"    data-original-title="Comments"  @click="OpenCommentpopup(data.row)">
                        <em class="fa fa-eye text-info"></em>
                    </a>
                </span>
            </template>
    </template>
    
</tg-list>
<DescriptionPopUp v-if="Commentpopup" @ClosePopup="ClosePopup" :assetTransferdetailId="assetTransferdetailId"
:assetTransferBatchId="assetTransferBatchId" :description="description" :relatedInfoId="relatedInfoId"></DescriptionPopUp>
</section>
</template>

<script>
import Schema from "../AssetCatalog/TransferedItemListSchema"
import DataService  from '../../service/DataService'
import DescriptionPopUp from "../../components/Modal/DescriptionPopUp.vue"
export default{
    components:{
        DescriptionPopUp
    },
   
    data()
    {
        return{
            View_List:[],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            LegendArray: [],
            TransfredList:[],
            PageSize:10,
            PageNumber: 1,
            PageSizeOption: null,
            StatusListForListing:[],
            Headers:[{
                        "COLUMN_NAME": 'name',
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Product Name",
                        "DISPLAY_ORDER": 1,
                        
                    },
                    {
                        "COLUMN_NAME": 'iteM_CODE',
                        "DATA_TYPE": "text",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Product Code",
                        "DISPLAY_ORDER": 2
                    },
                    {
                        "COLUMN_NAME": 'sourcE_LOCATION_NAME',
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Location From",
                        "DISPLAY_ORDER": 3
                    },
                    {
                        "COLUMN_NAME": 'destinatioN_LOCATION_NAME',
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Location To",
                        "DISPLAY_ORDER": 4,
                        
                        
                         
                    },
                    {
                        "COLUMN_NAME": 'transfeR_QUANTITY',
                        "DATA_TYPE": "text",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Shipped Quantity",
                        "DISPLAY_ORDER": 3,
                        settings : {
                            isInSlot : true
                        }
                       
                    },
                   
                    {
                        "COLUMN_NAME": 'accepteD_QUANTITY',
                        "DATA_TYPE": "text",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Accepted",
                        "DISPLAY_ORDER": 6,
                       
                    },
                    {

                        "COLUMN_NAME": 'rejecteD_QUANTITY',
                        "DATA_TYPE": "text",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Rejected",
                        "DISPLAY_ORDER": 7,
                        
                    },
                    { 
                        "COLUMN_NAME": 'disputeD_QUANTITY',
                        "DATA_TYPE": "text",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Disputed",
                        "DISPLAY_ORDER": 8,
                    }    ,
                    { 
                        "COLUMN_NAME": 'shippeD_BY',
                        "DATA_TYPE": "text",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Shipped By",
                        "DISPLAY_ORDER": 8,
                    },
                    { 
                        "COLUMN_NAME": 'description',
                        "DATA_TYPE": "text",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Comments",
                        "DISPLAY_ORDER": 8,
                        settings : {
                            isInSlot : true
                        }
                    }                         
                                          
            ],
            noRecord:true,
            TotalRecords:0,
            CurrentPage: 1,
            TotalPages: 0,
            SortBy: '',
            SortExp: '',
           
            catalogId:'',
            legendProgress:[{
                class:'',
                backgroundColor:'#17a2b8',
                name:'Draft'
            },
            {  class:'legend-Progress',
                backgroundColor:'',
                name:'Approved'
            },
            {
                class:'legend-pending',
                backgroundColor:'',
                name:'Pending'
            },
            {
                class:'',
                backgroundColor:'#fe6847',
                name:'SendForCorrection'
            },
            {
                class:'',
                backgroundColor:'#ee1313',
                name:'Rejected'
            }

        ],
        IsShowRelation:false,
        ShowClonePopup:false,
        ShowCheckAvailablity:false,
        ShowAddProduct:false,
        StatusIds:'',
        disputedVal:'',
        dateFormat:'',
        Commentpopup:false,
        assetTransferdetailId:'',
        assetTransferBatchId:'',
        itemName:'',
        OperationCode:'',
        relatedInfoId:'',
        description:''

        }
    },
   created:async function()
    {        
         var vueObj=this; 
         await vueObj.GetPageSize();
          await  vueObj.FetchData();
          Schema.bindleftCommonSearchdropdown(vueObj, "Asset", "Location From"); 
          Schema.bindleftCommonSearchdropdown(vueObj, "Asset", "Location To"); 
          Schema.bindleftCommonSearchdropdown(vueObj, "Asset", "Tag");  
          Schema.bindleftCommonSearchdropdown(vueObj, "Asset", "Status");        
          vueObj.LegendArray = [
                {
                    class: 'legend-Progress colorboxsquare',
                    text: "Uniquely named products"
                }
            ]
    },
    methods:{
        columnDataFormatter: function(row, column){
             return row[column.COLUMN_NAME];
           
        },
        async GetPageSize(){            
               var vm =this;
                var params='key=DefaultPageSize'
                await DataService.GetConfigurationValueForCompanyByKey(params).then((response) => {                  
                if(response.data!=null){                                    
                    vm.PageSize = parseInt(response.data);
                }                   
            });
        },
        async FetchData(){
            var vm = this;
            var params=`dateFormat=${vm.dateFormat}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&statusIds=${vm.StatusIds}&disputedVal=${vm.disputedVal}&${vm.searchCondition}`;
           await  DataService.GetTransferedItemsList(params).then(response=>{ 
                if(response.data!=null){
                    console.log('res',response.data)
                    if(response.data.length>0){                       
                            vm.TransfredList=response.data;
                            vm.TransfredList.forEach((row)=>{
                                row.additionalClass = '';
                                if (row.disputeD_QUANTITY > 0) {
                                   // row.additionalClass="warning-bar";
                                }
                                else if (row.haS_UNIUQE_NAMES == true){
                                       row.additionalClass = "low-bar odd";
                                }
                                if(row.IsSampleData == true){
                                    className = "sample-bar";
                                }
                            });
                            vm.TotalRecords = response.data[0].total;
                            vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                            vm.CurrentPage = vm.PageNumber;
                            vm.noRecord = false;
                    }
                    else{
                        vm.noRecord = true;
                        vm.ProductData = [];
                        vm.TotalRecords = 0
                    }
                }
                setTimeout(function () {
                    vm.ResponsiveDataTable('tablelistingdata')
                }, 500);   
            });
        },
      
        leftCommonSearch: function (value) { 
                         
        this.PageNumber = value.PageNumber;
        this.isPaged = 1;
        if (typeof value.searchCondition != 'undefined') {
            this.searchCondition = value.searchConditionJson
;
        }
        else {
            this.searchCondition = "";
        }
        this.FetchData();
       },
        currentPageMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        }, 
        GetPageSizeOption: function () {
            var list = [];
            var defaultPageSize = 10;
            for (var i = defaultPageSize; i < defaultPageSize + 95; i += 5) {
                list.push(i);
            }
            this.PageSizeOption = list;
        },
        pagerMethod: function (value) {
            
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },     
        commonHeaderButtonClick(event){                                
            switch (event.callbackfunction) {                
                 case "BackTolist":
                  this.BackTolist();
                  break;
                  case "ViewInTransitItems":
                   this.MoveToViewInTransitItems()
                 break;

            }
           
        },

        OpenCommentpopup(ths){
          
            var vm = this;
            vm.relatedInfoId=ths.relateD_INFO_ID,
            vm.assetTransferdetailId=ths.asseT_TRANSFER_DETAILS_ID;
            vm.assetTransferBatchId=ths.asseT_TRANSFER_BATCH_ID;
            vm.description=ths.name;
            vm.Commentpopup=true
        },
        ClosePopup:function(){
            var vm = this;
            vm.Commentpopup=false;
        },
       
        BackTolist()
        {   
                  
            this.$router.push({name: 'ProductListing'}); 
        },
           
        MoveToViewInTransitItems(){
           
            this.$router.push({name: 'ViewInTransitItems'}); 
        },
      
        sortdata: function (s) {
            
          var SortDir = "sort tb_headerSortDown";
           this.SortBy = s;
                if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else
                {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                }
               
                this.FetchData();
                $("#" + s+'  span:first-child').attr("class", SortDir);
            },

        renderActions(row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Clone':
                        action.isVisible = true;
                        break;
                    case 'Detail Products History':
                        action.isVisible = true;
                        break;
                    case 'View attached file(s)':
                        action.isVisible = true;
                        break;
                    default:
                        action.isVisible = true;
                        break;

                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        
    },
    
    
}

</script>
<style>
.vue-treeselect--searchable .vue-treeselect__input-container {
    padding: 0 !important;
}
</style>